@use "../global";

.service-contents {
	.container {
		.wrapper {
			position: relative;
			padding-bottom: 64px;
		}
	}
	.services {
		display: grid;
    justify-content: center;
    align-items: stretch;
    @include global.mq(xl) {
      grid-template-columns: repeat(auto-fit, minmax(352px, 1fr));
    }
    @include global.mq(lg) {
      grid-template-columns: 1fr 1fr;
    }
    @include global.mq(md) {
      grid-template-columns: 1fr;
    }
    gap: 32px;
    padding-left: 80px;
    padding-right: 80px;
    @include global.mq(lg) {
      padding-left: 24px;
      padding-right: 24px;
    }
    @include global.mq(md) {
      padding-left: 24px;
      padding-right: 24px;
      gap: 24px;
    }
		.service-article {
			box-sizing: border-box;
      box-shadow: 0 0 20px rgba(0,0,0,.1);
      border-radius: 8px;
			a {
        box-sizing: border-box;
        padding: 24px;
				display: block;
        height: 100%;
        @include global.mq(md) {
          display: flex;
          align-items: flex-start;
          justify-content: space-between;
          column-gap: 24px;
          padding: 16px;
        }
				&:hover {
					background-color: global.$light-green;
				}
			}
			figure {
				margin-left: auto;
				margin-right: auto;
				width: 180px;
				height: 180px;
				overflow: hidden;
				border-radius: 100%;
				@include global.background(center,center);
				margin-bottom: 24px;
        @include global.mq(md) {
          width: 96px;
          height: 96px;
          border-radius: 0;
          margin-bottom: 0;
        }
			}
			.detail {
        @include global.mq(md) {
          flex: 1;
        }
				h2 {
					text-align: center;
					font-weight: 600;
					margin-bottom: 12px;
					font-size: 1.8rem;
          @include global.mq(md) {
            font-size: 1.4rem;
            text-align: left;
            margin-bottom: 6px;
          }
				}
				p {
					font-size: 1.4rem;
          @include global.mq(md) {
            font-size: 1.2rem;
          }
				}
			}
		}
	}
	.service-top {
		.service-category {
			box-sizing: border-box;
			.title {
				padding: 0;
				margin-bottom: 16px;
			}
			.services {
				display: block;
				.service-article {
					margin-bottom: 0;
					flex-basis: auto;
					a {
						padding: 24px;
						@include global.flex(nowrap,space-between,stretch);
						figure {
							margin-left: 0;
							margin-right: 0;
							margin-bottom: 0;
						}
						.detail {
							margin-left: 24px;
							flex: 1;
							h2 {
								text-align: left;
							}
							p {
								margin-bottom: 0;
							}
						}
					}
				}
			}
		}
	}
}
